


















import {Vue , Component} from 'vue-property-decorator'

@Component({})
export default class ItemList extends Vue{
    [x: string]: any
    field={
        _id : { label:'ID' },
        head : {label:''},
        text : { text:'' },
        cover : { label:'' }
    }
    data = {}
    async fetch(){
        const res = await this.$http.get('item')
        this.data = res
    }
    created(){
        this.fetch()
    }
    async remove(row:any){
        await this.$confirm('')
        await this.$http.delete(`item/${row}`)
        this.$message.success('')
        this.fetch()
    }
}
