


















import {Vue , Component} from 'vue-property-decorator'

@Component({})
export default class UserList extends Vue{
    [x: string]: any
    data = {}
    field={
        _id:{ label:'ID' },
        username:{ label:'' }
    }
    
    async fetch(){ 
        const res = await this.$http.get('user')
        this.data = res
    }
    async remove(row:any){
        await this.$confirm('')
        await this.$http.delete(`user/${row}`)
        this.$message.success('')
        this.fetch()
    }
    created(){
        this.fetch()
    }
}
