


















import {Vue , Component} from 'vue-property-decorator'

@Component({})
export default class TechnologyList extends Vue{
    [x: string]: any
    data={
        watch:0,
        love:0,
        comment:{}
    }
    field={
        _id:{ label:"ID" },
        cover:{ label:'' },
        title:{ label:'' },
        category:{ label:'' },
        watch:{ label:'' },
        text:{ label:'' },
        love:{ label:'' },
        comment:{ label:'' },
        head:{ label:'' },
        name:{ label:'' }
    }
    async fetch(){
        const res = await this.$http.get('technology')
        this.data = res
    }
    async remove(row:any){
        await this.$confirm('')
        await this.$http.delete(`technology/${row}`)
        this.$message.success('')
        this.fetch()
    }
    created(){
        this.fetch()
    }
}
