


















import {Vue , Component} from 'vue-property-decorator'

@Component({
    
})
export default class LoveList extends Vue{
    [x: string]: any
    data={}
    field={
        _id:{label:'ID'},
        percentage:{label:''},
        cover:{label:''},
        text:{label:''},
        song:{label:''}
    }
    async fetch(){
        const res = await this.$http.get('mygirl')
        this.data = res
    }
    async remove(row:any){
        await this.$confirm('')
        await this.$http.delete(`mygirl/${row}`)
        this.$message.success('')
        this.fetch()
    }
    created(){
       this.fetch() 
    }
}
