



















import { Vue,Component } from 'vue-property-decorator'

@Component({})
export default class Main extends Vue {
    [x: string]: any
    data={}
    fields={
        _id:{label:'ID'},
        cover:{label:''},
        github:{label:'github'},
        qq:{ label:'qq' },
        vx:{  label:'' },
        qqkj:{ label:'' },
        bilibili:{ label:'' }
    }

    async fetch(){
        const res = await this.$http.get('home')
        this.data = res
    }

    async remove(row:any){
      await this.$confirm('')
      await this.$http.delete(`home/${row}`)
      this.$message.success('')
      this.fetch()
    }

    created(){
        this.fetch()
    }
}
