


















import {Vue , Component} from 'vue-property-decorator'

@Component({})
export default class LogList extends Vue{
    
    [x: string]: any
    field={
        _id:{label:'ID'},
        text:{ label:'' },
        cover:{ label:'' },
        createdAt: { label:''}
    }
    data={}
    async fetch(){
        const res = await this.$http.get('log')
        this.data = res
    }
    async remove(row:any){
        await this.$confirm('')
        await this.$http.delete(`log/${row}`)
        this.$message.success('')
        this.fetch()
    }
    created(){
        this.fetch()
    }
}
